import React,{useState} from "react";
import { useHistory } from "react-router-dom";
import { useDispatch,useSelector } from "react-redux";
import { withRouter } from "react-router";
import { CREATE_JOB_SHEET } from 'apiServices/action'
import Dropdown from "Component/Dropdown";
import Search from "Component/Search";
import { CSwitch } from "@coreui/react";
import { getStockTakeSummary,removeEntryModule,saveJobSheet } from "apiServices";
import SKUItem from "Component/SKUItem";
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";
import "./index.scss";
import { selectArea, moveAllStockMovement } from "./services";
import {selectSku} from './services'
import { filterCheckedSku, responseCheck,setSingleViewSku,setSwitch,updateSummary } from "Page/StockEntry/GeneralServices/moduleServices";
import PopUp from "Component/Modal/PopUp";
import PopUpUnfinished from 'Component/Modal/PopUpUnfinished'
import Discard from 'Component/Modal/PopUpDiscard'
import { ModalsConfirmation as Confirmation } from 'Component/Modal/ModalConfirmation'
import PopUpSaved from "Component/Modal/PopUpSaved"
import SingleView from 'Component/SingleViewModal'
import SKUPagination from 'Component/SKUPagination'
import ItemListModal from 'Component/ItemListModal'
import Scanner from 'Component/Scanner'
import {deleteSentSKU} from 'services'
import {useDimension, useStockMovement, useSingleView, useScanner} from 'Page/StockEntry/GeneralServices'
const submitCondition = ({setConfirmation, moveAllStockMovement,localEntryData, setLocalEntryData,setMoveAll}) => {
  setMoveAll(true)
  moveAllStockMovement({localEntryData,setLocalEntryData})
  setConfirmation({ show: true, name: 'submit' })
}

const StockMovement = (props) => {
  const story = useHistory()
  const dispatch = useDispatch()

  const {height,emptyItem,width} = useDimension(470)

  const {
    moduleName,
    thereAreRequest,
    moduleFirstLoad,
    warehouseUuid,
    stockMovementDropdown,
    areaId,
    areaIdDesposition,
    areaMovement,
    status,
    isLoading,
    stockMovementEntryData,
    summary,
    setSummary,
    localEntryData,
    setLocalEntryData,
    search,
    setSearch,
    area,
    areaMatched,
    discard,
    setDiscard,
    isCheckedTotal,
    entryIsReady,
    stockMovementTargetRepack
  } = useStockMovement({dispatch})

  const {
    updateList,
    action,
    setAction,
    confirmation,
    setConfirmation,
    setIsSuccess,
    isSaved,
    setIsSaved,
    createPo,
    setCreatePo,
    singleViewData,
    setSingleViewData,
    postInfo,
    moveAll, 
    setMoveAll,
    fromBarcode, 
    setFromBarcode,
  } = useSingleView({dispatch,setLocalEntryData,setSummary,setSearch,getStockTakeSummary,search,warehouseUuid,areaId, localEntryData,category})

  //search
  const [category, setCategory] = useState()

  const {showScanner, setShowScanner, showSkuList,setShowSkuList} = useScanner()
  
  const [selectAll, setSelectAll] = useState(false)

  //condition
  let isReady = false
  let isCheckedTrue = summary?.map(data => {
    if(data?.checked) return true
  })
  if(entryIsReady && areaMatched === true && thereAreRequest === 0 && !selectAll) isReady = true 
  if(areaMatched === true && thereAreRequest === 0 && selectAll && isCheckedTrue) isReady = true

  const noLength = summary?.length ? "flex-column" : "flex-row"
  const isPlural = isCheckedTotal > 1 ? "s" : ""
  const targetRepacks = useSelector(state => state.targetMovements)
  const dropdownMatched = areaId?.value && areaIdDesposition?.value && areaId?.value !== areaIdDesposition?.value
  const searchSku = (desc,category) => {
    dropdownMatched ?
    getStockTakeSummary({ 
      category,
      moduleName,
      stockMovementTargetRepack,
      dispatch,
      desc,
      warehouseUuid,
      areaId,
      areaIdDesposition ,
      setModule: setSummary,
      module:localEntryData  },
      setSearch(desc)) : dispatch({type:CREATE_JOB_SHEET, areaMovement:'empty'})
  }

  const searchCheck = ({areaId,areaIdDesposition, dispatch,searchSku, desc,category}) => {
    if(!areaId?.value){
      dispatch({type:CREATE_JOB_SHEET, areaId:'empty'})
      return
    }
    if(!areaIdDesposition?.value){
      dispatch({type:CREATE_JOB_SHEET, areaIdDesposition:'empty'})
      return
    }
    if(areaId?.value && areaIdDesposition?.value && areaId?.value === areaIdDesposition?.value){
      dispatch({type:CREATE_JOB_SHEET, areaIdDesposition:'empty'})
      return
    }
    else searchSku(desc,category)
  }
  return (
    <div className="w-100  stock-take-summary">
      <Confirmation
        moveAll={moveAll}
        singleViewData={singleViewData}
        noSkuChecking={true}
        isLoading={isLoading}
        moduleEntry={localEntryData}
        confirmation={confirmation}
        setConfirmation={setConfirmation}
        isSuccess={postInfo}
        setAction={setAction}
        action={action}
        submit={() => deleteSentSKU({
                        summary:summary,
                        resetSummary:setSummary,
                        name: 'submit',
                        moduleEntry:localEntryData,
                        singleViewData:singleViewData,
                        moduleName,
                        dispatch,
                        setIsSuccess,
                        saveJobSheet,
                        setLocalEntryData,
                        setCreatePo,
                        moveAll})}/>

      <PopUpSaved
        moduleEntry={moveAll ? localEntryData : singleViewData}
        submit={() => updateList()}
        confirmation={confirmation}
        setConfirmation={setConfirmation}
        isSaved={isSaved}
        action={action}
        setIsSaved={setIsSaved}
        moduleName={moduleName}
        totalSku={localEntryData?.jobPost?.list.length}
        setLocalEntryData={setLocalEntryData}
        moveAll={moveAll}/>
        
      <SingleView 
        fromBarcode={fromBarcode}
        moduleName={moduleName} 
        moduleEntry={localEntryData} 
        areaMovement={areaMovement} 
        createPo={createPo} 
        setCreatePo={setCreatePo} 
        setSingleViewData={setSingleViewData} 
        setConfirmation={setConfirmation}
        confirmation={confirmation}
        setMoveAll={setMoveAll}/>

      <Discard
        discard={discard}
        module={'Inventory Entry - Stock Movement'}
        moduleEntry={stockMovementEntryData}
        close={() => setDiscard(false)}
        discardEntry={(id) => removeEntryModule({ id, moduleName, dispatch,setDiscard })}/>

      <PopUpUnfinished
        discard={discard}
        moduleFirstLoad={moduleFirstLoad}
        moduleEntry={stockMovementEntryData}
        redirect={() => props.history.push("/inventory-entry/movement/weight-entry")}
        previousPage={() => story.goBack()}
        setDiscard={() => setDiscard(true)}/>

      <PopUp status={status} />

      <ItemListModal 
        areaDestination={areaIdDesposition}
        moduleName={moduleName}
        areaId={areaId} 
        areaName={area} 
        warehouseUuid={warehouseUuid} 
        showSkuList={showSkuList} 
        setShowSkuList={setShowSkuList} 
        dispatch={dispatch} 
        setLocalEntryData={setLocalEntryData} 
        localEntryData={localEntryData} 
        setCreatePo={setCreatePo} 
        thereAreRequest={thereAreRequest}
        setShowScanner={(value) => setShowScanner(value)}/>

      <Scanner 
        setLocalEntryData={setLocalEntryData}
        setShowSkuList={(value) => setShowSkuList(value)}
        showScanner={showScanner} 
        setShowScanner={(value) => setShowScanner(value)}
        setSearch = {(value) => setSearch(value)}
        search={search}
      />

      <div className="px-3 pt-3 bg-white border-all radius-all d-md-flex dd-group">
      {/* <div className="col-lg-3 px-0 stock-take-dropdown">
        <Dropdown
            show={true}
            title="Select Area"
            placeholder='Select Movement'
            options={stockMovementDropdown}
            onChangeDropdown={(selected) => selectArea({selected,area,dispatch})}
            selectedValue={areaMovement}
            showTitle={true}
          />
        </div> */}

        <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={true}
            title="Area Source"
            placeholder='Select Area Source'
            options={area}
            onChangeDropdown={(selected) => dispatch({type:CREATE_JOB_SHEET, areaId:selected})}
            selectedValue={areaId}
            showTitle={true}
          />
        </div>
        <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={true}
            title="Area Destination"
            placeholder='Select Area Destination'
            options={area}
            onChangeDropdown={(selected) => dispatch({type:CREATE_JOB_SHEET, areaIdDesposition:selected})}
            selectedValue={areaIdDesposition}
            showTitle={true}
          />
        </div>
      </div>

      <div className="pt-3 pb-0 pl-3 pr-3 bg-white mt-3 border-all radius-all">
      <div className="d-md-flex d-lg-flex justify-content-between mb-3">
          <div className="text LatoBold d-md-flex justify-content-between w-100 align-items-center sku-des">
            <div>
              Enter SKU or Description{" "}
              <span className="green">{`(${isCheckedTotal} Selected SKU${isPlural})`}</span>
            </div>
            <div className='d-flex align-items-center mt-2 mt-md-0 justify-content-between'>
              <span className='emptyTextField LatoBold mr-3'>Only show SKU by Target Movement</span>
              <CSwitch 
                className="float-right" 
                shape="pill" 
                color="success" size={width <= 500 ? "sm" : "md"} 
                checked={stockMovementTargetRepack} 
                defaultChecked={stockMovementTargetRepack} 
                onChange={() => dropdownMatched ? 
                  setSwitch({moduleName,search, updateList,dispatch,stockMovementTargetRepack, isMovement:true}) : 
                  searchCheck({areaId,areaIdDesposition, dispatch,searchSku,category}) } />
            </div>
          </div>
        </div>
        <Search
          category={category} 
          setCategory={setCategory}
          areaId={areaId}
          areaDestination={areaIdDesposition}
          moduleName={moduleName}
          // setAreaId={(selected) => selectArea({selected,area,dispatch})}
          setAreaId={(selected) => dispatch({type:CREATE_JOB_SHEET, areaId:selected})}
          setAreaDestination={(selected) => dispatch({type:CREATE_JOB_SHEET, areaIdDesposition:selected})}
          showScanner={true}
          barcodeScanner={(value) => {
            setShowScanner(value)
            setFromBarcode(true)
          }}
          placeholder={"Enter SKU"}
          filter={false}
          // searchHandler={(desc) =>searchSku(desc)}
          searchHandler={(desc) => searchCheck({areaId,areaIdDesposition, dispatch ,searchSku, desc,category})}
          setSearch={(value) => setSearch(value) }    
        />
        <div
          className={`mb-3 mt-3 d-flex align-items-center scroll-sku ${noLength} w-100 text-center`} style={{ minHeight: height , maxHeight:height}}>
          {summary?.length ? (
            summary?.map((data, idx) => {
              return (
                <SKUItem
                  skuUuId={data?.skuUuid}
                  selectAll={selectAll}
                  targetMovementProgress={data?.targetMovementProgress}
                  target={stockMovementTargetRepack}
                  key={idx}
                  img={data?.photo}
                  skuDescription={data?.skuName}
                  skuNumber={data?.skuNumber}
                  uomLabel="weight"
                  uomValue={data?.totalQty}
                  uom={data?.unitOfMeassures}
                  setSingleViewSku={(skuUuId) => setSingleViewSku({
                    summary,
                    setLocalEntryData,
                    warehouseUuid,
                    areaId,
                    areaIdDesposition,
                    skuUuId,
                    dispatch,
                    moduleName
                })}
                  isCheckedHandler={(checked) =>
                    selectAll? 
                    updateSummary({ summary, setSummary, idx, checked })  :
                    selectSku({
                      idx,
                      checked,
                      dispatch,
                      setLocalEntryData,
                      moduleSummary: summary,
                      moduleEntry: localEntryData,
                      moduleName,
                      warehouseUuid,
                      areaId,
                      areaIdDesposition,
                    })
                  }
                  check={data?.checked}
                  singleView={true}
                  createPo={createPo}
                  setCreatePo={setCreatePo}
                  setFromBarcode={setFromBarcode}
                />
              );
            })
          ) : (
              <div className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center" style={{ minHeight: emptyItem,maxHeight: emptyItem}}>
                {!responseCheck({ isLoading, summary }) ? (
                  <img className="loading" src={loading} alt="loading" />
                ) : areaId && areaIdDesposition && summary?.length === 0 ? (
                  <div>
                    Sorry, we couldn't find any matches
                    for '<span className='LatoBold'>{search}</span>' in {areaId?.label} area.
                    <br/>
                    Please Try search with another term.
                  </div>
                ) : 'Enter SKU'}
              </div>
            )}
        </div>
      </div>
      <SKUPagination setSelectAll={() => setSelectAll(!selectAll)} selectAll={selectAll}/>
      <div className="h-screen-summary-footer d-flex flex-column justify-content-start">
        <div className="w-100 d-flex align-items-center justify-content-between mb-1">
          <button
            tabIndex='0'
            onClick={() => props.history.push("/inventory-entry/movement/CSV-upload")}
            type="button"
            className="btn px-4 sku-item no-shadow border-all bg-white fontSizeButton"
          >
            Upload File
          </button>
          <div className='d-flex align-items-center'>
          <div onKeyDown={e => e.keyCode === 13 && isReady  ? submitCondition({setConfirmation, moveAllStockMovement,localEntryData,setLocalEntryData,setMoveAll}) : false} tabIndex='0' className={`${!entryIsReady ? 'tooltips' : 'tooltip-custom1'} w-100 sku-item d-flex align-items-center input-height px-2 mr-3`}>
          <span style={{cursor: isReady ? 'pointer' : 'context-menu'}} onClick={() => isReady ? submitCondition({setConfirmation, moveAllStockMovement,localEntryData,setLocalEntryData,setMoveAll}) : false} className='emptyTextField mr-4'>Move All</span>
              <span className={`${!entryIsReady ? 'tooltiptext' : 'd-none'}`}>
                  <span className='pl-1'>Please select the sku</span>
              </span>
          </div>
            <button
              tabIndex='0'
              onClick={() => {
                if (isReady) {
                  filterCheckedSku({
                    localEntryData,
                    moduleName,
                    summary,
                    dispatch,
                    warehouseUuid,
                    areaId,
                    areaIdDesposition,
                    setLocalEntryData
                  })
                  props.history.push("/inventory-entry/movement/weight-entry")
                }
              }
              }
              type="button"
              className={`btn px-5 sku-item fontSizeButton no-shadow text-white ${isReady ? "bg-green" : "bg-lineGrey"
                }`}
            >
                {(!localEntryData || localEntryData?.jobPost?.list?.length === 0) && thereAreRequest === 0 ? 'Next' :thereAreRequest === 0 && (localEntryData && localEntryData?.jobPost?.list?.length > 0) ? 'Next' : 'Processing...'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(StockMovement);
